<template>
  <div class="blog">
    <!-- Check if we are on the personal blog -->
    <custom-header
      v-if="$route.params.blog =='personal'"
      :heading="'My '+$route.params.blog+' Blog'"
      :image="personalImage"
    ></custom-header>
    <!-- Check if we are on the technical blog -->
    <custom-header
      v-else-if="$route.params.blog =='technical'"
      :heading="'My '+$route.params.blog+' Blog'"
      :image="technicalImage"
    ></custom-header>
    <!-- If we end up somewhere else -->
    <custom-header v-else :heading="heading" :subheading="subheading" :image="otherImage"></custom-header>

    <div class="col-lg-8 col-md-9 mx-auto">
      <div class="post-preview" id="posts" style="text-align: center">
        <!-- Display Descriptions -->
        <div v-for="post in posts">
          <post-description :title="post.title" :slug="post.slug" :date="post.created_at"></post-description>
          <hr>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// Import Libraries
const axios = require("axios");

// Import Components
import PostDescription from "@/components/post/Description.vue";

function buildUrl(blog) {
  return "http://localhost:8000/api/blog/" + blog;
}

export default {
  components: {
    "post-description": PostDescription
  },
  data() {
    return {
      heading: "Excuse Me...",
      subheading: "Sorry, that page does not exist.",
      personalImage: "blog/img/blog-header.jpg",
      technicalImage: "blog/img/laptop-blog.jpg",
      otherImage: "blog/img/glitch.png",
      posts: []
    };
  },
  created() {
    // fetch the data when the view is created and the data is
    // already being observed
    this.fetchData();
  },
  watch: {
    // call again the method if the route changes
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      let url = buildUrl(this.$route.params.blog);
      axios
        .get(url)
        .then(response => {
          this.posts = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
